.avatar {
  width: 50px;
  border-radius: 15%;
}
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 10px;
  padding: 6px;
  width: 15%;
  border-radius: 5%;
  box-shadow: 0 0 5px 5px rgba(0, 0, 0, 0.2);
}
.container:hover,
.container:focus {
  transform: scale(1.1, 1.1);
  transition: all 0.2s ease-in-out;
}
.name {
  word-break: break-all; 
  font-size: 16px;
  word-break: break-all;
  text-align: center;
  color: red;
}
